import stampit from 'stampit';
import { ObjectElement } from '@swagger-api/apidom-core';
import FallbackVisitor from "../../FallbackVisitor.js";
import MapVisitor from "../../generics/MapVisitor.js";
import { isReferenceLikeElement } from "../../../predicates.js";
import { isReferenceElement } from "../../../../predicates.js";
const PatternPropertiesVisitor = stampit(MapVisitor, FallbackVisitor, {
  props: {
    specPath: element => isReferenceLikeElement(element) ? ['document', 'objects', 'Reference'] : ['document', 'objects', 'Schema']
  },

  init() {
    this.element = new ObjectElement();
    this.element.classes.push('json-schema-patternProperties');
  },

  methods: {
    ObjectElement(objectElement) {
      // @ts-ignore
      const result = MapVisitor.compose.methods.ObjectElement.call(this, objectElement);
      this.element.filter(isReferenceElement).forEach(referenceElement => {
        referenceElement.setMetaProperty('referenced-element', 'schema');
      });
      return result;
    }

  }
});
export default PatternPropertiesVisitor;