import stampit from 'stampit';
import { test } from 'ramda';
import CallbackElement from "../../../../elements/Callback.js";
import PatternedFieldsJsonObjectVisitor from "../../generics/PatternedFieldsVisitor.js";
import FallbackVisitor from "../../FallbackVisitor.js";
import MapVisitor from "../../generics/MapVisitor.js";
import { isReferenceLikeElement } from "../../../predicates.js";
import { isReferenceElement, isPathItemElement } from "../../../../predicates.js";
const CallbackVisitor = stampit(PatternedFieldsJsonObjectVisitor, FallbackVisitor, {
  props: {
    fieldPatternPredicate: test(/{(?<expression>.*)}/),
    specPath: element => {
      // eslint-disable-next-line no-nested-ternary
      return isReferenceLikeElement(element) ? ['document', 'objects', 'Reference'] : ['document', 'objects', 'PathItem'];
    },
    canSupportSpecificationExtensions: true
  },

  init() {
    this.element = new CallbackElement();
  },

  methods: {
    ObjectElement(objectElement) {
      // @ts-ignore
      const result = MapVisitor.compose.methods.ObjectElement.call(this, objectElement); // decorate every ReferenceElement with metadata about their referencing type

      this.element.filter(isReferenceElement).forEach(referenceElement => {
        referenceElement.setMetaProperty('referenced-element', 'pathItem');
      }); // decorate every PathItemElement with Callback Object expression metadata

      this.element.filter(isPathItemElement).forEach((pathItemElement, key) => {
        pathItemElement.setMetaProperty('runtime-expression', key.toValue());
      });
      return result;
    }

  }
});
export default CallbackVisitor;